    }
  }, created() {
    // 取得物件與列印
    let id = sessionStorage.getItem('edmId');
    let c = sessionStorage.getItem('edmCode');

    let url = `http://21centuryedm.ddns.net/api/edm?id=${id}&c=${c}`;

    fetch(url, {})
      .then(res => {
        return res.json();
      }).then(formData => {
        // 若物件不符合模板轉跳回首頁
        console.log(formData.data.edm.user);
        this.Packages = formData.data.edm.user;
      })


