<template>
  <div class="edm-1-2 w-1240 mx-auto">
    <!-- EDM預覽 -->
    <section class="section py-0">
      <div class="container-fluid p-0">
        <!-- EDM風格一 -->
        <div class="edm-style-1-2">
          <!-- head -->
          <EdmHeader />
          <div class="body pb-5 px-120 default-bg">
            <div class="mainContent d-flex justify-content-between">
              <div v-for="items in Packages.items" class="objectCard pr-4">
                <div class="image">
                  <img :src="`${items.image}`" alt="object image">
                  <div class="slogan px-4 py-2">
                    <span>我們為你</span>
                    <span>找到21世紀的家</span>
                  </div>
                </div>

                <div class="title py-3 px-4 d-flex align-items-center justify-content-center">
                  <span>
                    {{ items.name }}
                  </span>
                </div>

                <div class="total-box box py-3 d-flex align-items-center justify-content-center">
                  <div class="total">總價 $ <b class="text-danger">
                      {{ items.price }}</b> 萬</div>
                </div>

                <div class="baseInfo box">
                  <div class="title mb-3">物件基本資料</div>
                  <ul class="list">
                    <li style="height:90px">
                      <div class="label">地址</div>
                      {{ items.county }}{{ items.district }}{{ items.address }}
                    </li>
                    <li>
                      <div class="label">總坪數</div>
                      <span class="text-danger">{{ items.total_floor_space }}</span> 坪
                    </li>
                    <li>
                      <div class="label">格局</div>
                      {{ items.structure_room }}房｜{{ items.structure_hall }}廳｜{{ items.structure_bath }}衛
                    </li>
                    <li>
                      <div class="label">樓層</div>
                      {{ items.floor }}樓
                    </li>
                    <li>
                      <div class="label">屋齡</div>
                      {{ items.age }}年
                    </li>
                    <li>
                      <div class="label">種類</div>
                      {{ items.type }}
                    </li>
                    <li>
                      <div class="label">車位</div>
                      {{ items.if_parking_space }}
                    </li>
                  </ul>
                </div>
              </div>

            </div>
          </div>
          <!-- footer -->
          <EdmFooter />
        </div>
      </div>
    </section>

  </div>
</template>

<script>
import EdmFooter from '@/components/EdmFooter.vue';
import EdmHeader from '@/components/printPages/EdmHeader.vue';
import { useRoute } from 'vue-router';

export default {
  components: {
    EdmHeader,
    EdmFooter
  }, data() {
    return {
      Packages: [],
    }
  }, created() {

    // 取得物件與列印
  let id = sessionStorage.getItem('edmId');
    let c = sessionStorage.getItem('edmCode');

    let url = `http://21centuryedm.ddns.net/api/edm?id=${id}&c=${c}`;


    fetch(url, {})
      .then(res => {
        return res.json();
      }).then(formData => {
        let edmLength = formData.data.edm.items.length;
        // 若物件不符合模板轉跳回首頁
        if (edmLength != 2) {
          alert('物件數量不符合此風格！即將轉跳回首頁');
          window.location.href = '/';
        } else {
          this.Packages = formData.data.edm;
        }
      })

  }
}
</script>

