<script>
import $ from 'jquery';
import NavBar2 from '@/components/NavBar2.vue';
import { useRoute } from 'vue-router';

// 頁面按鈕
import Paginate from "vuejs-paginate-next";

// 排序引用
import dictionary from '@/assets/data/dictionary.json'

export default {
  name: 'century',
  components: {
    NavBar2,
    paginate: Paginate,
  }, data() {
    const route = useRoute();

    let { keyword, address, store_name, county, district,
      floor, structure, age, min_floor_space, max_floor_space, min_total_floor_space, max_total_floor_space, min_price, max_price, date_range, type, status, d_type
    } = route.query;

    return {
      keyword: keyword,
      address: address,
      store_name: store_name,
      county: county,
      district: district,
      floor: floor,
      structure: structure,
      age: age,
      min_floor_space: min_floor_space,
      max_floor_space: max_floor_space,
      min_total_floor_space: min_total_floor_space,
      max_total_floor_space: max_total_floor_space,
      min_price: min_price,
      max_price: max_price,
      date_range: date_range,
      type: type,
      status: status,
      d_type: d_type,

      page: '',

      Packages: [],
      // 排序
      selectAnswer: '',


      // 點擊收藏功能
      isFavorite: true,

    }

  }, mounted() {
    // 這裡寫jQuery
    $(document).ready(function () {
      // 按鈕文字顏色
      $('.pagination').attr('class', 'pagination mb-0');
      $('.page-link').css('border', 'transparent');
      $('.page-link').css('color', 'black');
      $('li.page-item').css('color', 'black');
      // $('.page-item.active .page-link').css('border', 'transparent')

      // 按下一頁後的樣式
      $('.page-item').css('background-color', 'transparent');

    });

  }, methods: {
    // sortByAge() {
    // if (this.selectAnswer == 'ageH2L') {
    //   this.Packages.items.sort((a, b) => b[this.Packages.items.age] > a[this.Packages.items.age] ? 1 : -1)
    // } else if (this.selectAnswer == 'ageL2H') {
    //   this.Packages.items.sort((a, b) => b[this.Packages.items.age] > a[this.Packages.items.age] ? -1 : 1)
    // }
    // },

    // 頁面按鈕：更換頁面重新取得下一頁資料&返回最上頁
    clickCallback(pageNum) {
      console.log(pageNum)
      const route = useRoute();

      let { keyword, address, store_name, county, district,
        floor, structure, age, min_floor_space, max_floor_space, min_total_floor_space, max_total_floor_space, min_price, max_price, date_range, type, status, d_type
      } = route.query;

      let url = `http://21centuryedm.ddns.net/api/estate-century/21century?page=${pageNum}&keyword=${keyword}&address=${address}&store_name=${store_name}&county=${county}&district=${district}&floor=${floor}&structure=${structure}&age=${age}&min_floor_space=${min_floor_space}&max_floor_space=${max_floor_space}&min_total_floor_space=${min_total_floor_space}&max_total_floor_space=${max_total_floor_space}&min_price=${min_price}&max_price=${max_price}&date_range=${date_range}&type=${type}&status=${status}&d_type=${d_type}`
      console.log(url);

      let token = sessionStorage.getItem('token');

      console.log(token);

      fetch(url, {
        headers: {
          Authorization: `Bearer ${token}`,
          token: token
        }
      }).then(res => {
        return res.json();
      }).then(formData => {
        this.Packages = formData.data

        console.log(formData);
        console.log('===測試===');
        // console.log(Packages);
        console.log(this.Packages);
        // console.log(this.Packages.items);
        // console.log(formData.data);
        // console.log(formData);
      })
    },


    // 收藏按鈕功能
    changeFavorite(id) {
      let type = 'estate_century';

      let token = sessionStorage.getItem('token');
      let url = `http://21centuryedm.ddns.net/api/favorite/toggle`;

      fetch(url, {
        headers: {
          Authorization: `Bearer ${token}`,
          token: token,
          'Accept': 'application/json',
          'Content-Type': 'application/json',
        },
        method: 'post',

        body: JSON.stringify(
          {
            id: id,
            type: type
          }
        )
      }).then(res => {
        console.log(res);
        return res.json();
      }).then(res => {
        let favorite = res.data.is_favorite;
        let favoriteID = document.getElementById('favorite' + id);


        if (favorite == 1) {
          favoriteID.innerHTML = '';
          favoriteID.innerHTML += `<i class="fa-solid fa-heart" style="color:#C0AC79;"></i>`
          alert('您已收藏此物件！')
        } else if (favorite == 0) {
          favoriteID.innerHTML = '';
          favoriteID.innerHTML += ` <i class="fa-regular fa-heart" style="color:#C0AC79;"></i>`
          alert('您已移除收藏此物件！')
        }

      })
    },

  }, computed: {

    sortAge(a, b) {
      return this.Packages.items.sort((a, b) => a.age - b.age)
    }
  }, watch: {
    // 更換選項執行排序
    selectAnswer(NewVal, OldVal) {
      console.log(NewVal);
    },
    // 更換頁碼返回最上頁
    page: (NewVal, OldVal) => {
      console.log(NewVal, OldVal, '頁面切換,返回最上頁');
      window.scrollTo(0, 0);
    }

  },
  created() {
    // 搜尋欄的值
    const route = useRoute();

    let { keyword, address, store_name, county, district,
      floor, structure, age, min_floor_space, max_floor_space, min_total_floor_space, max_total_floor_space, min_price, max_price, date_range, type, status, d_type
    } = route.query;


    // 獲取資料
    let url = `http://21centuryedm.ddns.net/api/estate-century/21century?page=1&keyword=${keyword}&address=${address}&store_name=${store_name}&county=${county}&district=${district}&floor=${floor}&structure=${structure}&age=${age}&min_floor_space=${min_floor_space}&max_floor_space=${max_floor_space}&min_total_floor_space=${min_total_floor_space}&max_total_floor_space=${max_total_floor_space}&min_price=${min_price}&max_price=${max_price}&date_range=${date_range}&type=${type}&status=${status}&d_type=${d_type}`
    console.log(url);

    let token = sessionStorage.getItem('token');

    fetch(url, {
      headers: {
        Authorization: `Bearer ${token}`,
        token: token
      }
    }).then(res => {
      return res.json();
    }).then(formData => {
      this.Packages = formData.data
      console.log('===測試===');
      // console.log(Packages);
      console.log(this.Packages);

      // 頁碼
      if ((Packages.pagination.total_pages) <= 0) {
        Packages.pagination.total_pages = 1
      } else {
        Packages.pagination.total_pages = Packages.pagination.total_pages;
      }
    })
  },
}

