<template>
  <NavBar3 />
  <div class="container-fluid">
    <p class="text-muted mb-1">EDM內容簡介</p>
    <div class="y-container mx-auto my-3">


      <edm1_1 v-if="`${useEDM}` == 1" />
      <edm1_2 v-if="`${useEDM}` == 2" />
      <edm1_4 v-if="`${useEDM}` == 4" />
      <edm1_6 v-if="`${useEDM}` == 6" />
      <edm1_8 v-if="`${useEDM}` == 8" />


    </div>
  </div>

  <div class="edm-box mx-auto mb-1 px-3">
    <p>EDM連結：</p>
    <div class="input-group mb-3">
      <input type="text" class="form-control" readonly id="txt_output" :value="`${edmUrl}`">
      <div class="input-group-append">
        <button class="btn btn-outline-secondary" type="button" id="copy" v-on:click="copyurl">複製連結</button>
      </div>
    </div>
  </div>


  <div class="objectItmes mx-auto px-3">
    <div class="objectName">
      <p class="mb-1">物件名稱：</p>
      <input type="text" class="form-control mb-3" v-model="title" id="txt_output">
    </div>
    <div class="objectContent"></div>
  </div>





  <!--發送內容-->
  <div id="send_section" class=" mx-auto px-3">
    <div class="form-group">
      <label>內文 *</label>
      <textarea class="form-control" v-model="content" name="content" rows="10" required></textarea>
    </div>
    <p>業務人員：</p>
    <p>業務人員聯絡方式：</p>
  </div>


  <div id="home_section" class="d-flex justify-content-center w-100 px-3 mb-3">
    <button type="button" class="btn btn-primary w-100" v-on:click="shareThis()" id="send">確定傳送</button>
  </div>

</template>



<script>
import edm1_1 from '@/components/emdPages/style1-1.vue'
import edm1_2 from '@/components/emdPages/style1-2.vue'
import edm1_4 from '@/components/emdPages/style1-4.vue'
import edm1_6 from '@/components/emdPages/style1-6.vue'
import edm1_8 from '@/components/emdPages/style1-8.vue'

// Nav
import NavBar3 from '@/components/NavBar3.vue';


export default {
  components: {
    edm1_1,
    edm1_2,
    edm1_4,
    edm1_6,
    edm1_8,

     // Nav
     NavBar3
  },
  data() {
    let domain = window.location.origin;
    let tem = sessionStorage.getItem('temStyle');
    let edmId = sessionStorage.getItem('edmId');
    let edmCode = sessionStorage.getItem('edmCode');
    return {
      edmUrl: domain + `/edm/${tem}` + `?id=${edmId}&c=${edmCode}`,
      Packages: [],
      useEDM: 0,

      // 定義傳送物件
      title: '',
      content: '',

    }
  },
  mounted() {




  }, methods: {
    // 複製
    copyurl() {
      var copyText = document.getElementById("txt_output");
      copyText.select();
      copyText.setSelectionRange(0, 99999)
      document.execCommand("copy");
      alert('連結已複製')
      //$('#copy_text').text('連結已複製');
      setTimeout(function () {
        //  $('#copy_text').text('複製連結');
      }, 2000)
    },

    // 分享
    shareThis() {
      // 判斷瀏覽器是否支援 Web Share API
      const shareData = {
        title: `${this.title}`,
        text: `${this.content}`,
        url: `${this.edmUrl}`
      }

      try{
        navigator.share(shareData);
      }
      catch(err){
        console.log(err);
      }

    }

  }, created() {

    let edmId = sessionStorage.getItem('edmId');
    let edmCode = sessionStorage.getItem('edmCode');

    let url = `http://21centuryedm.ddns.net/api/edm?id=${edmId}&c=${edmCode}`;

    fetch(url)
      .then(res => {
        console.log(res);
        return res.json();
      })
      .then(result => {
        console.log(result);
        let edmLength = result.data.edm.items.length;
        console.log(edmLength);

        this.useEDM = edmLength;
        console.log(this.useEDM);

        this.Packages = result.data.edm;
      })

  }
}
</script>




