<template>
  <div class="edm-1-4 w-1240 mx-auto">
    <!-- EDM預覽 -->
    <section class="section py-0">
      <div class="container-fluid p-0">
        <!-- EDM風格一 -->
        <div class="edm-style-1-4">
          <!-- head -->
          <EdmHeader />
          <div class="body px-120 default-bg">
            <div class="mainContent d-flex justify-content-between flex-wrap">
              <div v-for="(items, index) in Packages.items" class="objectCard w-100 pr-4 mb-5 d-flex">


                <!-- 左半部 -->
                <div class="objectLeft">
                  <div class="image">
                    <img :src="`${items.image}`" alt="object image">
                  </div>
                  <div class="title d-flex align-items-center justify-content-center py-3 px-4">
                    <span>
                      {{ items.name }}
                    </span>

                    <!-- 編號 -->
                    <div class="objectNo">
                      {{ index+ 1 }}
                    </div>
                  </div>


                </div>
                <!-- 右半部 -->
                <div class="objectRight col-6 p-0 m-0">
                  <div class="baseInfo box">
                    <ul class="list d-flex">
                      <div class="col-4">
                        <li>
                          <div class="label">總坪數</div>
                          <span class="text-danger">{{ items.total_floor_space }}</span> 坪
                        </li>
                        <li>
                          <div class="label">格局</div>
                          {{ items.structure_room }}房｜{{ items.structure_hall }}廳｜{{ items.structure_bath }}衛
                        </li>

                      </div>


                      <div class="col-6">
                        <li>
                          <div class="label">地址</div>
                          {{ items.county }}{{ items.district }}{{ items.address }}
                        </li>

                        <li>
                          <div class="label">種類</div>
                          {{ items.type }}
                        </li>
                      </div>

                    </ul>
                  </div>
                  <div class="total-box box py-3 d-flex align-items-center justify-content-center">
                    <div class="total">總價 $ <b class="text-danger">
                        {{ items.price }}</b> 萬</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- footer -->
          <EdmFooter />
        </div>
      </div>
    </section>

  </div>
</template>

<script>
import EdmFooter from '@/components/EdmFooter.vue';
import EdmHeader from '@/components/EdmHeader.vue';
import { useRoute } from 'vue-router';

export default {
  components: {
    EdmHeader,
    EdmFooter
  }, data() {
    return {
      Packages: [],
    }
  }, created() {
       // 取得物件與列印
  let id = sessionStorage.getItem('edmId');
    let c = sessionStorage.getItem('edmCode');

    let url = `http://21centuryedm.ddns.net/api/edm?id=${id}&c=${c}`;



    fetch(url, {})
      .then(res => {
        return res.json();
      }).then(formData => {
        let edmLength = formData.data.edm.items.length;
        // 若物件不符合模板轉跳回首頁
        if (edmLength != 4) {
          alert('物件數量不符合此風格！即將轉跳回首頁');
          window.location.href = '/';
        } else {
          this.Packages = formData.data.edm;
        }
      })

  }
}
</script>

