<script>
import { useRoute } from 'vue-router'
import EdmFooter from '@/components/EdmFooter.vue';

export default {
  components: {
    EdmFooter,
  },
  data() {
    return {
      Packages: [],
    }
  },
  created() {

    // 取得物件與列印
    let edmId = sessionStorage.getItem('edmId');
    let edmCode = sessionStorage.getItem('edmCode');

    let url = `http://21centuryedm.ddns.net/api/edm?id=${edmId}&c=${edmCode}`;

    fetch(url, {})
      .then(res => {
        console.log(res);
        return res.json();
      }).then(formData => {
        console.log(formData);
        let edmLength = formData.data.edm.items.length;
        if (edmLength != 1) {
          alert('物件數量不符合此風格！即將轉跳回首頁');
          window.location.href = '/';
        } else {
          // 若物件不符合模板轉跳回首頁
          this.Packages = formData.data;
        }
      })

  }
}
</script>
