<template>
  <div class="edm-1-8 w-1240 mx-auto">
    <!-- EDM預覽 -->
    <section class="section py-0">
      <div class="container-fluid p-0">
        <!-- EDM風格一 -->
        <div class="edm-style-1-8">
          <!-- head -->
          <EdmHeader />
          <div class="body px-50 default-bg">
            <div class="mainContent d-flex justify-content-between flex-wrap">

              <div v-for="(items, index) in Packages.items" class="objectCard pr-4 mb-3 d-flex">
                <!-- 左半部 -->
                <div class="objectLeft">
                  <div class="image">
                    <img :src="`${items.image}`" alt="object image">
                  </div>
                  <div class="title py-1 px-4 d-flex align-items-center justify-content-center">
                    <span>
                      {{ items.name }}
                    </span>

                    <!-- 編號 -->
                    <div class="objectNo">
                      {{ index + 1 }}
                    </div>
                  </div>
                  <div class="total-box py-1 box d-flex align-items-center justify-content-center flex-column">
                    <div class="total">總價 $
                      <b class="text-danger">{{ items.price }}</b>
                      萬
                    </div>
                  </div>

                  <!-- 右半部 -->
                  <div class="objectRight p-0 m-0">
                    <div class="baseInfo box">
                      <ul class="list d-flex flex-column">
                        <div class="col-12 p-0 m-0">
                          <li style="height: calc(54px * 1.5);">
                            <div class="label">地址</div>
                            {{ items.county }}{{ items.district }}{{ items.address }}
                          </li>
                        </div>
                        <div class="col-12 p-0 m-0 d-flex">
                          <div class="col-5 p-0 m-0 mr-2">
                            <li>
                              <div class="label">總坪數</div>
                              <span class="text-danger">{{ items.total_floor_space }}</span> 坪
                            </li>


                            <li>
                              <div class="label">種類</div>
                              {{ items.type }}
                            </li>
                          </div>
                          <div class="col-6 p-0 m-0">

                            <li>
                              <div class="label mb-1">格局</div>
                              {{ items.structure_room }}房｜{{ items.structure_hall }}廳｜{{ items.structure_bath }}衛
                            </li>

                          </div>
                        </div>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- footer -->
          <EdmFooter />
        </div>
      </div>
    </section>

  </div>
</template>

<script>
import EdmFooter from '@/components/EdmFooter.vue';
import EdmHeader from '@/components/EdmHeader.vue';
import { useRoute } from 'vue-router';

export default {
  components: {
    EdmHeader,
    EdmFooter
  }, data() {
    return {
      Packages: [],
    }
  }, created() {
    // // 取得物件數量判定是符合此模板
    // let itemsArr = items.split(',')

    // 取得物件與列印
    const route = useRoute();
    let { id, c } = route.query;
    let url = `http://21centuryedm.ddns.net/api/edm?id=${id}&c=${c}`;


    fetch(url, {})
      .then(res => {
        return res.json();
      }).then(formData => {
        let edmLength = formData.data.edm.items.length;
        // 若物件不符合模板轉跳回首頁
        if (edmLength != 8) {
          alert('物件數量不符合此風格！即將轉跳回首頁');
          window.location.href = '/';
        } else {
          this.Packages = formData.data.edm;
        }
      })

  }
}
</script>

