<script>
import $ from 'jquery';
import NavBar2 from '@/components/NavBar2.vue';
import { useRoute } from 'vue-router';

// 頁面按鈕
import Paginate from "vuejs-paginate-next";

// 讀取圖示
import Loading from 'vue-loading-overlay';
// import {useLoading} from 'vue-loading-overlay'

export default {
  name: 'conpetitor',
  components: {
    NavBar2,
    paginate: Paginate,

    // Loding
    Loading
  }, data() {

    return {
      isHidden: true,

      page: '',
      Packages: [],
      PerPage: [],
      PageSize: 50,

      // Loding....
      // LoaderVisiable: false
      loader: 'dots',
    }
  }, computed: {
    // 頁數
    MaxPage() {
      var MaxPage = Math.ceil(this.Packages.length / this.PageSize);

      if(MaxPage == 0){
        var MaxPage = 1;
      }else{
        var MaxPage = Math.ceil(this.Packages.length / this.PageSize);
      }
      console.log(MaxPage);

      return MaxPage;
    },

  },
  methods: {
    // 頁面按鈕：更換頁面重新取得下一頁資料
    clickCallback(pageNum) {
      console.log(pageNum);
      // 第N頁載入第(N-1)*50筆到第N*50筆資料
      let NextPage = this.Packages.slice(((pageNum - 1) * this.PageSize), (pageNum * this.PageSize))
      console.log(NextPage);

      this.PerPage = NextPage
    },

  }, mounted() {
    // jQuery
    $(document).ready(function () {
      // 按鈕文字顏色
      $('.pagination').attr('class', 'pagination mb-0');
      $('.page-link').css('border', 'transparent');
      $('.page-link').css('color', 'black');
      $('li.page-item').css('color', 'black');

      // 按下一頁後的樣式
      $('.page-item').css('background-color', 'transparent');

    });

  }, watch: {
    sortByOption: {
      handler(value) {
        this.sortByAge()
      },
      deep: true
    },
    // 頁面返回
    page: (OldVal, NewVal) => {
      console.log(OldVal, NewVal, '頁面切換,返回最上頁');
      window.scrollTo(0, 0);
    },

  }, created() {
    // 獲取資料
    let token = sessionStorage.getItem('token');

    let url = `http://172.105.217.8/get_datas`;

    // 擷取前頁面關鍵字
    const route = useRoute();
    let { keyword, address, store_name, county, district,
      floor, structure, age, min_floor_space, max_floor_space, min_total_floor_space, max_total_floor_space, min_price, max_price, date_range, type, status, d_type
    } = route.query;


    this.isLoading = true;


    // Fetch取得資料
    fetch(url, {
      headers: {
        // token: token,
        Authorization: `Bearer ${token}`,
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      },
      method: 'POST',

      // 搜尋資料請求
      body: JSON.stringify({
        "hAge_end": age,
        "h_Main_Pin_num_start": max_floor_space,
        "h_Main_Pin_num_end": min_floor_space,
        "hPin_num_start": max_total_floor_space,
        "hPin_num_end": min_total_floor_space,
        "hPrice_num_start": max_price,
        "hPrice_num_end": min_price,
        "floor_end": floor,
        // "hCity": county,
        "hKind": type,
        "hSource": 20,
        "hArea": district,
        "hStatus": status,
        "hTitle": keyword,
        // "hComm": keyword,
        // "hAddress": address,
      })
    }).then(res => {
      console.log(res);
      return res.json();
    })
      .then(formData => {
        this.isLoading = false;
        this.isHidden = false;


        console.log(formData.results);
        // 先取50頁
        let arr = formData.results;
        this.PerPage = arr.slice(0, this.PageSize);
        this.Packages = formData.results;



      })
  }
}


</script>
